// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-cobalt:                      #003eb7;
$color-torch-red:                   #ed0233;
$color-venetian-red:                #b80027;
$color-dark-cerulean:               #003d85;
$color-crimson:                     #d10a42;
$color-smalt:                       #00359F;
$color-sapphire:                    #102654;
$color-egyptian-blue:               #103aae;
$color-resolution-blue:             #1c3374;
$color-black-russian:               #030a1a;
$color-light-slates-grey:           #74819c;
$color-ghost-white:                 #f8f9fc;


// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

$ff-basic: "Roboto Condensed", "Arial", sans-serif;
$ff-highlight: "Alumni Sans", "Calibri", sans-serif;

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
