@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-extrabold.woff2") format("woff2"), url("../fonts/alumnisans-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-bold.woff2") format("woff2"), url("../fonts/alumnisans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-semibold.woff2") format("woff2"), url("../fonts/alumnisans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/rorobotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/rorobotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/rorobotocondensed-light.woff2") format("woff2"), url("../fonts/robotocondensed-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #102654;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 clamp(45px, 7.5vw, 103px);
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767px) {
  .footer .container {
    padding-top: 50px;
    padding-bottom: 50px;
    flex-direction: column;
    align-items: flex-start;
  }
}

.btn {
  display: inline-flex;
  padding: 19px 56px;
  color: #ffffff;
  background-color: transparent;
  border: 4px solid #ffffff;
  cursor: pointer;
  font-weight: 700;
  font-size: 12px;
  line-height: 13px;
  align-items: center;
  justify-content: center;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
    background: linear-gradient(97.05deg, #003eb7 17.6%, #ed0233 95.31%);
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
  background: linear-gradient(97.05deg, #003eb7 17.6%, #ed0233 95.31%);
}
.btn--dark {
  color: #102654;
  border: 4px solid #102654;
}
@media (hover: hover) {
  .btn--dark:hover:not(.focus-visible) {
    color: #ffffff;
    border-color: transparent;
    border-left: none;
    border-right: none;
    padding: 10px 24px;
    background: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  }
}
.btn--dark.focus-visible:focus {
  color: #ffffff;
  border-color: transparent;
  border-left: none;
  border-right: none;
  padding: 10px 24px;
  background: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
}
@media (max-width: 1199px) {
  .btn {
    padding: 20px 30px;
  }
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
  .custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
  .custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
  .custom-input label textarea,
  .custom-textarea label input,
  .custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label,
  .custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
  .custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.logo {
  color: #ffffff;
}

.logo__image {
  fill: currentcolor;
}
@media (max-width: 1199px) {
  .logo__image {
    width: 80px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .logo__image {
    width: 66px;
    height: 24px;
  }
}

.footer {
  background-color: #030a1a;
  color: #ffffff;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
}
@media (max-width: 767px) {
  .footer__wrapper {
    display: block;
    columns: 2;
  }
}

.footer__site-list {
  display: flex;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
  gap: 30px;
}
@media (max-width: 767px) {
  .footer__site-list-item {
    width: 50%;
  }
}

.footer__social-list {
  display: flex;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
  flex-grow: 1;
  gap: 11px;
  justify-content: flex-end;
}

.footer__social-icon {
  fill: currentcolor;
}

.header {
  height: fit-content;
  padding: 30px 0 50px 0;
  background: conic-gradient(from 150.87deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header {
    padding-top: 40px;
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 15px;
    padding-bottom: 80px;
  }
}

.header__links {
  position: relative;
  display: flex;
  justify-content: space-between;
  min-height: max-content;
  z-index: 1;
}

.header__banner {
  display: grid;
  grid-template-columns: max-content min-content max-content;
  gap: 16px;
}
@media (max-width: 1199px) {
  .header__banner {
    gap: 22px;
  }
}
@media (max-width: 767px) {
  .header__banner {
    position: relative;
    padding-bottom: 62px;
    grid-template-columns: max-content min-content;
    justify-content: center;
    gap: 0;
  }
}

.header__list {
  margin: 0;
  padding-left: 20px;
  padding-bottom: 188px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
  list-style: none;
  align-self: end;
}
@media (max-width: 1199px) {
  .header__list {
    padding-left: 0;
    padding-bottom: 140px;
  }
}
@media (max-width: 767px) {
  .header__list {
    padding-bottom: 55px;
    margin-right: -13px;
  }
}

.header__text {
  display: flex;
  padding-top: 90px;
  flex-direction: column;
  align-items: flex-end;
}
.header__text h1 {
  margin: 0;
  padding-top: 20px;
  padding-bottom: 48px;
  font-family: "Alumni Sans", "Calibri", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 96px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}
.header__text p {
  margin: 0;
  font-family: "Alumni Sans", "Calibri", sans-serif;
  font-weight: 600;
  font-size: 40px;
  line-height: 45px;
  letter-spacing: 0.07em;
  padding-right: 50px;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .header__text {
    padding-top: 75px;
  }
  .header__text h1 {
    padding-top: 10px;
    padding-bottom: 37px;
    font-weight: 800;
    font-size: 76px;
    line-height: 86px;
    letter-spacing: 1px;
    text-transform: none;
  }
  .header__text p {
    padding-right: 3px;
    font-size: 28px;
    line-height: 30px;
    letter-spacing: 2px;
  }
}
@media (max-width: 767px) {
  .header__text {
    padding-top: 65px;
  }
  .header__text h1 {
    padding-top: 15px;
    padding-bottom: 23px;
    font-weight: 800;
    font-size: 76px;
    line-height: 86px;
    letter-spacing: 1px;
  }
  .header__text p {
    padding-right: 0;
    font-size: 24px;
    line-height: 30px;
  }
}

@media (max-width: 767px) {
  .header__btn-link {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
  }
}

.header__image {
  margin-top: -30px;
  padding-left: 50px;
  width: clamp(100%, 36vw, 500px);
  object-fit: cover;
}
@media (max-width: 1199px) {
  .header__image {
    margin-top: 0;
    padding-top: 17px;
    padding-left: 8px;
    width: clamp(100%, 42vw, 324px);
  }
}
@media (max-width: 767px) {
  .header__image {
    display: none;
  }
}

.phone {
  font-family: "Alumni Sans", "Calibri", sans-serif;
  font-style: normal;
